@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
}


@-webkit-keyframes scroll {
	0% { -webkit-transform: translateX(0); transform: translateX(0); }
	100% { -webkit-transform: translateX(calc(-800px * 2)); transform: translateX(calc(-800px * 2))}
}


@keyframes scroll {
	0% { -webkit-transform: translateX(0); transform: translateX(0); }
	100% { -webkit-transform: translateX(calc(-800px * 2)); transform: translateX(calc(-800px * 2))}
}


.slider {
	background: white;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 500px;
	margin: auto;
	overflow:hidden;
	position: relative;
	width: 1260px;
}
	.slider::before,
	.slider::after {
		@include white-gradient;
		content: "";
		height: 500px;
		position: absolute;
		width: 200px;
		z-index: 2;
	}
	
	.slider::after {
		right: 0;
		top: 0;
		-webkit-transform: rotateZ(180deg);
		        transform: rotateZ(180deg);
	}

	.slider::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		-webkit-animation: scroll 40s linear infinite;
		        animation: scroll 40s linear infinite;
		display: -webkit-flex;
		display: flex;
		width: calc(1000px * 4);
	}
	
	.slide {
		height: 500px;
		width: 1000px;
	}
.track{
